
body{background-color:#fafafa
}
header{width:1080px;margin:auto;padding-left:25px
}
.nav{font-size:36px;width:1080px;height:200px;margin-bottom:30px;padding-left:107px;padding-right:107px
}
.nav .nav-List li{width:154px;float:left;font-size:21px;text-align:center;padding-top:88px
}
.nav .nav-List li:hover{background:-webkit-linear-gradient(#3da1ff 100%, transparent 100%) no-repeat center top/92px 6px;background:linear-gradient(#3da1ff 100%, transparent 100%) no-repeat center top/92px 6px;cursor:pointer
}
.nav .nav-List li:hover a{color:#3da1ff
}
.nav-floatLiast{width:1080px;display:none
}
.nav-floatLiast ul li{width:154px;float:left;font-size:17px
}
.nav-floatLiast ul li i{font-size:62px;color:#3da1ff
}



